



















import Heart from '@/components/heart.vue' 
import {
	getRGB
} from '@/common/heart.js'
export default {
	components:{
		Heart
	},
	data() {
		return {
			heartList:[]
		}
	},
	onLoad() {

	},
	methods: {
		pageMove(e){
			this.heartList.push({
				left:e.touches[0].clientX,
				top:e.touches[0].clientY,
				color:getRGB()
			})
		}
	}
}
